@tailwind base;
@tailwind components;
@tailwind utilities;
@tailwind variants;
@font-face {
  /* https://noonnu.cc/font_page/1202 */
  font-family: 'HakgyoansimBareondotumB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-2@1.0/HakgyoansimBareondotumB.woff2')
    format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  /* https://noonnu.cc/font_page/1215 */
  font-family: 'HakgyoansimGodeunjemokB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-2@1.0/HakgyoansimGodeunjemokB.woff2')
    format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  /* https://noonnu.cc/font_page/1223 */
  font-family: 'HakgyoansimWoojuR';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-2@1.0/HakgyoansimWoojuR.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  /* https://noonnu.cc/font_page/694 */
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

body {
  background-color: #0d1117;
}
:root {
  @apply text-gray-50;
}

* {
  font-family: 'HakgyoansimWoojuR', sans-serif;
}

.font-highlight {
  font-family: 'HakgyoansimBareondotumB', sans-serif;
}

.font-info {
  font-family: 'Pretendard-Regular', sans-serif;
}

.font-hw {
  font-family: 'Dancing Script', cursive;
}
